import { useEffect, useState } from 'react';
import gsap from 'gsap';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Autoplay } from "swiper/modules";
import 'swiper/css';

function Slider(){
	let [currentNum, setCurrentNum]=useState(0);
	let [winw, setWinw]=useState(0);
	let [progressTween, setProgressTween]=useState(null);

	const offsetData={
		shadowInfo: [
			{left: "0%", width: "50%"},
			{left: "50%", width: "40%"},
			{left: "0%", width: "40%"},
		],
		textInfo: [
			{left: "55%"},
			{left: "5%"},
			{left: "45%"},
		]
	};

	const titleData=[
		{
			title: "Tmap Mobility",
			subject: "<dt>반응형 프로젝트 제작</dt><dd><span>현대 Xteer 사이트 리뉴얼</span><span>HTML5 + CSS3 + jQuery</span><span>제작기간 3주</span></dd>",
			link: "https://yunyoung522.github.io/project/project1"
		},
		{
			title: "한국도자기",
			subject: "<dt>반응형 프로젝트 제작</dt><dd><span>HTML5 + CSS3 + jQuery</span><span>제작기간 3주</span></dd>",
			link: "https://yunyoung522.github.io/project/project2"
		},
		{
			title: "Wylie",
			subject: "<dt>반응형 프로젝트 제작</dt><dd><span>HTML5 + CSS3 + jQuery</span><span>제작기간 3주</span><dd>",
			link: "https://yunyoung522.github.io/project/project3"
		}
	];

	useEffect(() => {
		window.addEventListener("resize", () => {
			resizeHandler(currentNum);
		});
	}, [currentNum]);

	let slideInit=swiper => {
		let currentNumInit=swiper.realIndex;
		let totalNum=swiper.slides.length;

		resizeHandler(currentNum);

		let mainSlider=document.querySelector(".main_slider");
		let [slider, swiperControl, swiperPageControl]=mainSlider.children;
		let [swiperWrapper, keyText, shadow]=slider.children;
		let swiperList=swiperWrapper.children;

		let [title, subject, link]=keyText.children;
		let [current, progressbar, length]=swiperControl.children;
		let [prevBtn, nextBtn]=swiperPageControl.children;

		keyText.classList.remove("active");

		current.innerText="0"+(currentNum+1);
		length.innerText="0"+totalNum;

		setProgressTween(gsap.to(progressbar.firstElementChild, {width: "100%", duration: 8}));

		let video=document.getElementById("bg_video");
		video.muted=true;
	
		video.addEventListener("loadeddata", function(){
			video.play();
		});
		video.addEventListener("ended", function(){
			video.play();
		});

		let w=window.innerWidth;

		setTimeout(function(){
			for(let i=0; i<swiperList.length; i++){
				if(swiperList[i].className.indexOf("list"+(currentNum+1)) !== -1){
					swiperList[i].firstElementChild.classList.add("active");
				}
			}

			keyText.classList.add("active");

			if(w > 720){
				keyText.style.left=offsetData.textInfo[currentNum].left;
			}
			else{
				keyText.style.left="30px";
			}

			shadow.style.left=offsetData.shadowInfo[currentNum].left;
			shadow.style.width=offsetData.shadowInfo[currentNum].width;

			title.innerHTML=titleData[currentNum].title;
			subject.innerHTML=titleData[currentNum].subject;
			link.setAttribute("href", titleData[currentNum].link);
		}, 1200);

		prevBtn.addEventListener("click", e => {
			e.preventDefault();
			swiper.slidePrev();
		});
		nextBtn.addEventListener("click", e => {
			e.preventDefault();
			swiper.slideNext();
		});
	};

	let resizeHandler=(n) => {

		let keyText=document.querySelector(".main_slider .keytext");
		let w=window.innerWidth;

		setWinw(w);
		

		if(w > 720){
			keyText.style.left=offsetData.textInfo[n].left;
		}
		else{
			keyText.style.left="30px";
		}
	};

	let slideChange=swiper => {
		let currentNumInit=swiper.realIndex;
		let totalNum=swiper.slides.length;

		setCurrentNum(currentNumInit);

		
		let mainSlider=document.querySelector(".main_slider");
		let [slider, swiperControl]=mainSlider.children;
		let [swiperWrapper, keyText, shadow]=slider.children;
		let swiperList=swiperWrapper.children;

		let [title, subject, link]=keyText.children;
		let [current, progressbar, length]=swiperControl.children;

		keyText.classList.remove("active");

		for(let i=0; i<swiperList.length; i++){
			swiperList[i].firstElementChild.classList.remove("active");
		}

		current.innerText="0"+(currentNum+1);
		length.innerText="0"+totalNum;

		progressTween.pause();
		progressTween.seek(0);
		progressTween.play();
		progressbar.firstElementChild.removeAttribute("style");
		resizeHandler(currentNum);
		
		setTimeout(function(){
			for(let i=0; i<swiperList.length; i++){
				if(swiperList[i].className.indexOf("list"+(currentNumInit+1)) !== -1){
					swiperList[i].firstElementChild.classList.add("active");
				}
			}

			keyText.classList.add("active");

			if(winw > 720){
				keyText.style.left=offsetData.textInfo[currentNumInit].left;
			}
			else{
				keyText.style.left="30px";
			}

			shadow.style.left=offsetData.shadowInfo[currentNumInit].left;
			shadow.style.width=offsetData.shadowInfo[currentNumInit].width;

			title.innerHTML=titleData[currentNumInit].title;
			subject.innerHTML=titleData[currentNumInit].subject;
			link.setAttribute("href", titleData[currentNumInit].link);
		}, 1200);
	};

	return(
		<div className="main_slider">
			<Swiper
				loop={true}
				allowTouchMove={false}
				autoplay={{
					delay: 7000,
					disableOnInteraction: false
				}}

				onSwiper={swiper => {
					slideInit(swiper);
				}}

				onSlideChange={swiper => {
					slideChange(swiper);
				}}

				modules={[Autoplay]}
				className="swiper-container mainSwiper"
			>
				<SwiperSlide className="list1">
					<span className="area">
						<video id="bg_video" src="./assets/video/bg-kv.mp4"></video>
					</span>
				</SwiperSlide>
				<SwiperSlide className="list2">
					<span className="area">
						<img src="./assets/images/Hankook.jpg" alt="main2" />
					</span>
				</SwiperSlide>
				<SwiperSlide className="list3">
					<span className="area">
						<img src="./assets/images/wylie.jpg" alt="main3" />
					</span>
				</SwiperSlide>
				<div className="keytext">
					<strong className="title">Tmap Mobility</strong>
					<div className="subject">
						<dl>
							<dt>Responsible web</dt>
							<dd>
								<span>HTML5 + CSS3</span>
								<span>React Native</span>
								<span>SASS</span>
							</dd>
						</dl>
					</div>
					<a href="/" className="link" target="portfolio"><h3>사이트 바로가기</h3></a>
				</div>
				<div className="shadow"></div>
			</Swiper>
			<div className="swiper_control">
				<span className="current">00</span>
				<div className="progressbar"><span></span></div>
				<span className="total">00</span>
			</div>
			<div className="page_control">
				<a href="/" className="bt_prev">prev</a>
				<a href="/" className="bt_next">next</a>
			</div>
			<div class="port_guide">
				<div class="inner">

					<a href="https://yunyoung522.github.io/project/pdf/Resume.pdf" target="portpolio">
						Resume(pdf)
						<span></span>
					</a>
				</div>
			</div>

			{/*
				<div className="swiper-container mainSwiper">
					<div className="swiper-wrapper">
						<div className="swiper-slide list1">
							<span className="area">
								<video id="bg_video" src="/assets/video/bg-kv.mp4"></video>
							</span>
						</div>
						<div className="swiper-slide list2">
							<span className="area">
								<img src="/assets/images/Hankook.jpg" alt="main2" />
							</span>
						</div>
						<div className="swiper-slide list3">
							<span className="area">
								<img src="/assets/images/wylie.jpg" alt="main3" />
							</span>
						</div>
					</div>
					<div className="keytext">
						<strong className="title">Tmap Mobility</strong>
						<div className="subject">
							<dl>
								<dt>Responsible web</dt>
								<dd>
									<span>HTML5 + CSS3</span>
									<span>React Native</span>
									<span>SASS</span>
								</dd>
							</dl>
						</div>
						<a href="/" className="link" target="portfolio"><h3>사이트 바로가기</h3></a>
					</div>
					<div className="shadow"></div>
				</div>
				<div className="swiper_control">
					<span className="current">00</span>
					<div className="progressbar"><span></span></div>
					<span className="total">00</span>
				</div>
				<div className="page_control">
					<a href="/" className="bt_prev">prev</a>
					<a href="/" className="bt_next">next</a>
				</div>
			*/}
		</div>
	);
}

export default Slider;